/*初始化dayjs的相关配置*/
import Vue from "vue";
import dayjs from "dayjs";
import 'dayjs/locale/zh-cn' //引入中文语言
import relativeTime from 'dayjs/plugin/relativeTime'

//配置使用处理相对时间
dayjs.extend(relativeTime)
//配置使用中文语言包
dayjs.locale('zh-cn')
//把处理相对时间的代码包装为全局过滤器
//然后就可以在任何组件的模板中使用了
//所谓的过滤器其实就是一个可以在模板中调用的函数而已
//在组件的模板中使用过滤器：{{xxx」relativeTime}
//管道符前面的内容会作为参数传递给过滤器函数
//过滤器的返回值会渲染到使用过滤器的模板中
//日期格式化
const now=dayjs().format('YYYY年MM月DD日 HH时:mm分:ss秒')
Vue.filter('relativeTime',value=>{
    console.log(value);return now
})