<template>
  <div class="todo-footer" v-show="total">    <!-- 通过total的数量来控制是否显示页面 ，如果todo为0，就是total为0，即为false 否则为true   -->
    <label>
      <input type="checkbox" :checked="isAll" v-on:change="checkInput"/>
<!--      <input type="checkbox" v-model="isAll"/   第二种 用v-model绑定total  >-->
    </label>
    <span>
          <span>已完成{{doneTotal}}</span> / 全部{{total}}
        </span>
    <button class="btn btn-danger" @click="deleteTodo">清除已完成任务</button>
  </div>
</template>

<script>
export default {
  name: "MyFooter",
  props: ['todos', 'selectAll','deleteAllDone'],
  computed: {
    // 判断是否所有的todo要勾选
    isAll() {
      return this.doneTotal === this.total && this.total > 0
    },


    //todo的个数
    total() {
      return this.todos.length;
    },
    // 计算已完成的个数
    // doneTotal(){ let i = 0;this.todos.forEach((todo)=>{if (todo.done) i++});return i}, 第一种写法
    //第二种写法  数组的reduce()统计函数
    // const x =this.todos.reduce((pre,todoObj)=>{console.log('@',pre,todoObj);return pre+(todoObj.done?1:0)},0);
    // return x;
    doneTotal() {
      //简写
      return this.todos.reduce((pre, todoObj) => pre + (todoObj.done ? 1 : 0), 0);
    }
  },
  methods: {
    //全选与取消全选
    checkInput(e) {
      this.selectAll(e.target.checked)
    },
    //删除所有已完成
    deleteTodo(){
      this.deleteAllDone();
    },

  }
}
</script>

<style scoped>
/*footer*/
.todo-footer {
  height: 40px;
  line-height: 40px;
  padding-left: 6px;
  margin-top: 5px;
}

.todo-footer label {
  display: inline-block;
  margin-right: 20px;
  cursor: pointer;
}

.todo-footer label input {
  position: relative;
  top: -1px;
  vertical-align: middle;
  margin-right: 5px;
}

.todo-footer button {
  float: right;
  margin-top: 5px;
}

</style>