//该文件夹创建vuex最为核心的store

//引入vuex
import Vuex from 'vuex'
//引入vue
import Vue from 'vue';
//准备action-用于响应组件中的动作
const actions = {}
// 准备mutations-用于操作数据（state）
const mutations = {}
// 准备stste-用于存储数据
const state = {
    year:new Date().getFullYear(),
    month:new Date().getMonth(),
    day:new Date().getDay(),

}
//使用Vuex插件
Vue.use(Vuex);
//创建store并暴露Store
export default new Vuex.Store({
    // actions:action,
    // mutations:mutations,
    // state:state
    // 以上简写
    actions,
    mutations,
    state,
});
