<template>
  <li>
    <label>
      <input type="checkbox" v-bind:checked="todo.done" @click="handleCheck(todo.id)" />
      <span v-show="!todo.isEdit">{{todo.title}}</span>
      <input v-show="todo.isEdit" type="text" :value="todo.title" @blur="handleBlur(todo,$event)" ref="inputTitle">
    </label>
    <button class="btn btn-danger" @click="handleDelete(todo.id)" style="">删除</button>
    <button class="btn btn-edit" @click="handleEdit(todo)" v-show="!todo.isEdit">编辑</button>
  </li>
</template>

<script>
export default {
  name: "MyItem",
  props: ['todo', 'checkTodo','deleteTodo'], //声明接收todo对象,checkTodo方法
  methods: {
    //勾选或取消勾选
    handleCheck(id) {  //当input框的状态改变时，就输出id
      this.checkTodo(id);
    },
    //删除
    handleDelete(id) {
      if(confirm("您确定要删除嘛?Really?")){
        this.deleteTodo(id);
      }
    },
    //控制编辑按钮是否显示输入框
    handleEdit(todo){
      this.$set(todo,'isEdit',true);//为todo添加自定义属性，实现数据同步响应
      this.$nextTick(function (){this.$refs.inputTitle.focus()}) //点击编辑时自动聚焦输入框
    },
    //input框失去焦点触发
    handleBlur(todo,e){
      todo.isEdit=false;
      if (!e.target.value.trim()) return alert('输入不能为空');
      this.$bus.$emit('updateTodo',todo.id,e.target.value);

    }
  }
}
</script>

<style scoped>
/*item*/
li {
  list-style: none;
  height: 36px;
  line-height: 36px;
  padding: 0 5px;
  border-bottom: 1px solid #ddd;
}

li label {
  float: left;
  cursor: pointer;
}

li label li input {
  vertical-align: middle;
  margin-right: 6px;
  position: relative;
  top: -1px;
}

li button {
  float: right;
  display: none;
  margin-top: 3px;
}

li:before {
  content: initial;
}

li:last-child {
  border-bottom: none;
}
li:hover button{
  display: block;
  /*background: red;*/
}li:hover {
  background: darkgrey;
}
</style>