<template>
  <div class="todo-container">
    <div class="todo-wrap">
      <!--   向myhead组件传入receive方法属性，用于获取myhead组件中input中的值 -->
      <MyHeader v-bind:addToDo="addToDo"></MyHeader>
      <!--  向mylist组件传入todos对象属性-->
      <MyList v-bind:todos="todos" v-bind:checkTodo="checkTodo" v-bind:deleteTodo="deleteTodo" />
      <MyFooter v-bind:todos="todos" v-bind:selectAll="seleceAll" v-bind:deleteAllDone="deleteAllDone"/>
    </div>
  </div>
</template>

<script>
import MyHeader from "@/components/MyHeader";
import MyList from "@/components/MyList";
import MyFooter from "@/components/MyFooter";
export default {
  name: "ToDoList",
  components: {MyHeader,MyList,MyFooter},
  data() {
    return {
      //要传入mylist组件里面的数据
      // todos: [{id: '001', title: '喝酒', done: true}, {id: '002', title: '抽烟', done: false}, {
      //   id: '003',
      //   title: '烫头',
      //   done: true
      // },]
      todos: JSON.parse(localStorage.getItem('todos')) || [],  //从本地存储取出数据,因为json解析空为null，导致无法检测数组，再加一个【】
    };
  },
  methods:{
    // 接收myhead组件传入的对象  添加一个todo
    addToDo(todoObj) {
      this.todos.unshift(todoObj);  //把接收的myhead对象传入数组todos
    },
    //勾选或者取消勾选一个todo
    checkTodo(id) {
      this.todos.forEach((todoObj) => {
        if (todoObj.id === id) todoObj.done = !todoObj.done
      })
    },
    //删除一个todo
    deleteTodo(id) {
      // this.todos=this.todos.filter(todo => todo.id != id ); 也可以这样写
      this.todos.forEach((todoObj) => {
        if (todoObj.id === id) this.todos.shift(todoObj)
      })
    },
    //全选与取消全选
    seleceAll(all) {
      this.todos.forEach((todo) => {
        todo.done = all
      })
    },
    //清空所有已完成
    deleteAllDone() {
      this.todos = this.todos.filter((todo) => {
        return !todo.done
      })
    },
  },
  watch:{
    todos: {
      deep: true,  //深度监测
      handler(newvalue,oldvalue) {
        localStorage.setItem('todos', JSON.stringify(newvalue));  //存入本地存储
      },
    },
  },
}
</script>

<style >
/*base*/
body {
  background: #fff;
}

.btn {
  display: inline-block;
  padding: 4px 12px;
  margin-bottom: 0;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2), 0 1px 2px rgba(0, 0, 0, 0.05);
  border-radius: 4px;
}

.btn-danger {
  color: #fff;
  background-color: #da4f49;
  border: 1px solid #bd362f;
}
.btn-edit{
  color: #ffffff;
  background-color: #099659;
  border: 1px solid #42b983;
}

.btn-danger:hover {
  color: #fff;
  background-color: #bd362f;
}

.btn:focus {
  outline: none;
}

.todo-container {
  width: 600px;
  margin: 0 auto;
}
.todo-container .todo-wrap {
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
}
</style>