<template>
<div>
  <h1>今天是{{new Date().getFullYear()}}年{{new Date().getMonth()+1}}月{{new Date().getDate()}}日</h1>
  <h3>本月还剩下</h3>
  <my-progress type="circle" :percentage="month" ></my-progress>
</div>
</template>

<script>
export default {
  name: "Month",
  computed:{
    month(){
      return Math.floor(new Date().getDate()/31*100);
    }
  }
}
</script>

