<template>
<div>
  <h1>今天是{{new Date().getMonth()+1}}月{{new Date().getDate()}}日{{nowweek}}</h1>
  <h3>本周还剩下</h3>
  <my-progress type="circle" :percentage="weekday" ></my-progress>
</div>
</template>

<script>
export default {
  name: "Week",
  data(){
    return{
       week:new Date().getDay(),
       weekArr:[
        "星期日",
        "星期一",
        "星期二",
        "星期三",
        "星期四",
        "星期五",
        "星期六",
      ],
    }
  },
  computed:{
    nowweek(){
      return this.weekArr[this.week]
    },
    weekday(){
      if (this.week==0){return 100}
      return Math.ceil((this.week/7)*100);
    },
  }


}
</script>

