<template>
<!--<div>现在是北京时间：{{"牛逼" | relativeTime}}</div>-->
  <div class="outer-table">
    <Circle :percent="80">
      <span class="demo-Circle-inner" style="font-size:24px">80%</span>
    </Circle>
    <div class="inner-table">
        <Button @click="anim1 = !anim1">显示当前时间</Button>
        <transition name="move-right">
          <div v-show="anim1" class="transition-box">
            <div>{{nowDate}}</div>
            <div>{{nowTime}}</div>
            <div>{{nowWeek}}</div>
          </div>
        </transition>

    </div>
  </div>
</template>

<script>
export default {
  name: "TimeHeader",
  data(){
    return{
      timer: "", //定义一个定时器
      nowWeek:"", //星期
      nowDate: "", //日期
      nowTime: "", // 当前日期时间（具体到秒）
      anim1:false,
    }
  },
  methods:{
    getTime() {
      this.timer = setInterval(() => {
        let timeDate = new Date();
        let year = timeDate.getFullYear();
        let mounth = timeDate.getMonth() + 1;
        let day = timeDate.getDate();
        let hours = timeDate.getHours();
        hours = hours >= 10 ? hours : "0" + hours;
        let minutes = timeDate.getMinutes();
        minutes = minutes >= 10 ? minutes : "0" + minutes;
        let seconds = timeDate.getSeconds();
        seconds = seconds >= 10 ? seconds : "0" + seconds;
        let week = timeDate.getDay();
        let weekArr = [
          "星期日",
          "星期一",
          "星期二",
          "星期三",
          "星期四",
          "星期五",
          "星期六",
        ];
        this.nowDate = `${year}/${mounth}/${day}`;
        this.nowTime = `${hours}:${minutes}:${seconds}`;
        this.nowWeek =  `${weekArr[week]}`;
        // this.nowTime = `${year}/${mounth}/${day} ${hours}:${minutes}:${seconds} ${weekArr[week]}`
      }, 1000);
    }
  },
  mounted() {
    this.getTime();
  },
  beforeDestroy() {
    if (this.timer) {
      clearInterval(this.timer);
    }
  }
}

</script>

<style scoped>
.outer-table {
  display: table;
}
.inner-table {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
}
.transition-box {
  margin-top: 32px;
  width: 100%;
  border-radius: 4px;
  background-color: #409EFF;
  text-align: center;
  color: #fff;
  padding: 24px 0;
}
</style>