<template>
  <div class="todo-header">
    <!--     v-model="InputValue"   用v-model也行 -->
    <input type="text" placeholder="请输入你的任务名称，按回车键确认" v-on:keyup.enter="add" />
  </div>
</template>

<script>
import {nanoid} from 'nanoid'; //引入自动id生成器
export default {
  name: "MyHeader",
  data(){
    return{
      InputValue:'',
    };
  },
  methods:{
    add(e){
      if (e.target.value=='') alert('输入框为空') //判断输入框是否为空
      else {
        const todoObj = {id: nanoid(), title: e.target.value, done: false}; // 把input输入的值包装成一个对象
        this.addToDo(todoObj); //调用APP传入的方法，把对象传进去，
        e.target.value = '';  //清空输入框
      }
    },
                 },
  props:['addToDo'],   //接收传入的receive方法
}
</script>

<style scoped>
/*header*/
.todo-header input {
  width: 560px;
  height: 28px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 4px 7px;
}

.todo-header input:focus {
  outline: none;
  border-color: rgba(82, 168, 236, 0.8);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(82, 168, 236, 0.6);
}
</style>